import { Button, makeStyles, Modal, Snackbar } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import axios from "axios";
import React, { useState } from "react";
import { _SERVER } from "../../addProductDatas/addProductDatas";

const EditProductImage = ({
  openEditProductImage,
  setOpenEditProductImage,
  product,
  setProducts,
  setLoading,
  setOpenLoginModal,
}) => {
  const [image, setImage] = useState("");
  const [message, setMessage] = useState("");
  const [error, setError] = useState(false);
  const [open, setOpen] = useState(false);

  const handleSubmitImage = (e) => {
    e.preventDefault();
    let imageFormData = new FormData();
    imageFormData.append("image", image);
    const token = localStorage.getItem("token20hvin");
    if (token) {
      setLoading(true);
      axios({
        method: "post",
        url: `${_SERVER}/api/products/updateProductImage/${product._id}/${product.category}`,
        data: imageFormData,
        headers: {
          "content-type": "multipart/form-data",
          Authorization: "Bearer " + token,
        },
      })
        .then((response) => {
          if (response && response.data.status === 200) {
            setError(false);
            setProducts(response.data.data);
            setMessage(response.data.message);
            setTimeout(() => {
              setOpenEditProductImage(false);
            }, 2000);
          } else {
            setError(true);
            setMessage(response.data.message);
          }
        })
        .catch((error) => console.log(error))
        .finally(() => setLoading(false));
    } else {
      setOpenEditProductImage(false);
      setOpenLoginModal(true);
    }
  };
  const useStyles = makeStyles((theme) => ({
    root: {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      backgroundColor: "white",
      padding: theme.spacing(1),
    },
    modal: {
      marginTop: "50%",
    },
    input: {
      display: "none",
    },
  }));
  const classes = useStyles();
  return (
    <Modal
      className={classes.modal}
      open={openEditProductImage}
      onClose={() => setOpenEditProductImage(false)}
    >
      <form className={classes.root} onSubmit={handleSubmitImage}>
        <Snackbar
          open={message ? true : false}
          autoHideDuration={5000}
          onClose={() => setOpen(false)}
        >
          <Alert
            severity={error ? "error" : "success"}
            onClose={() => setOpen(false)}
          >
            {message}
          </Alert>
        </Snackbar>
        <input
          className={classes.input}
          accept="image/*"
          id="updateImage"
          files={image}
          type="file"
          onChange={(e) => {
            setImage(e.target.files[0]);
          }}
        />
        <label htmlFor="updateImage">
          <Button
            type="button"
            fullWidth
            variant="contained"
            color="secondary"
            component="span"
            style={{ margin: "5px" }}
          >
            Modifier l'image de {product.title}
          </Button>
        </label>
        {image && (
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            style={{ margin: "5px" }}
          >
            Envoyer l'image pour {product.title}
          </Button>
        )}
      </form>
    </Modal>
  );
};

export default EditProductImage;
