import {
  Box,
  Button,
  Chip,
  Collapse,
  Container,
  FormControl,
  IconButton,
  Input,
  InputLabel,
  makeStyles,
  MenuItem,
  Select,
  TextField,
  Typography,
  useTheme,
} from "@material-ui/core";
import { AddCircle, Cancel } from "@material-ui/icons";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import { formInfos, _SERVER } from "../../addProductDatas/addProductDatas";

const AddProduct = ({
  products,
  setProducts,
  showAddProduct,
  setShowAddProduct,
  setOpenLoginModal,
}) => {
  const currentCategory = useParams();
  const useStyles = makeStyles((theme) => ({
    root: {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      backgroundColor: "white",
      padding: theme.spacing(1),
    },
    formControl: {
      margin: theme.spacing(1),
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
    chips: {
      display: "flex",
      flexWrap: "wrap",
    },
    chip: {
      margin: 2,
    },
    noLabel: {
      marginTop: theme.spacing(3),
    },
    input: {
      display: "none",
    },
  }));
  function getStyles(name, tagName, theme) {
    return {
      fontWeight:
        tagName.indexOf(name) === -1
          ? theme.typography.fontWeightRegular
          : theme.typography.fontWeightMedium
    };
  }
  const theme = useTheme();
  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 250,
      },
    },
  };
  const classes = useStyles();
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [price, setPrice] = useState("");
  const [category, setCategory] = useState("");
  const [tagName, setTagName] = useState([]);
  const [image, setImage] = useState("");

  const handleChangeTagSelect = (event) => {
    setTagName(event.target.value);
  };
  const handleChangeCategorySelect = (event) => {
    setCategory(event.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    let formData = new FormData();
    formData.append("title", title);
    formData.append("description", description);
    formData.append("price", price);
    formData.append("category", category);
    formData.append("tag", tagName || []);
    formData.append("image", image);

    const token = localStorage.getItem("token20hvin");
    if (token) {
      axios({
        method: "post",
        url: `${_SERVER}/api/products/createProduct`,
        data: formData,
        headers: {
          "content-type": "multipart/form-data",
          Authorization: "Bearer " + token,
        },
      })
        .then((response) => {
          setProducts(response.data.data);
        })
        .then(() => {
          setTitle("");
          setDescription("");
          setPrice("");
          setTagName([]);
          setImage("");
        });
    } else {
      setOpenLoginModal(true);
    }
  };
  useEffect(() => {
    setCategory(currentCategory.category);
  }, [currentCategory]);
  return (
    <Container>
      <Typography style={{ textAlign: "center" }} variant="h4">
        Ajouter un Produit
      </Typography>
      <Box style={{ display: "flex", justifyContent: "center" }}>
        <IconButton onClick={() => setShowAddProduct(!showAddProduct)}>
          {!showAddProduct ? (
            <AddCircle fontSize="large" style={{ color: "green" }} />
          ) : (
            <Cancel fontSize="large" style={{ color: "red" }} />
          )}
        </IconButton>
      </Box>
      <Collapse in={showAddProduct}>
        <form
          className={classes.root}
          onSubmit={handleSubmit}
          encType="multipart/form-data"
        >
          <TextField
            id="title"
            label="Nom du produit"
            fullWidth
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            required
          />
          <TextField
            id="description"
            fullWidth
            multiline
            label="Description du produit"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            rows={4}
          />
          <TextField
            id="price"
            fullWidth
            label="Prix du produit"
            required
            type="number"
            InputProps={{ inputProps: { min: 0, step: 0.5 } }}
            value={price}
            onChange={(e) => setPrice(e.target.value)}
          />
          <FormControl className={classes.formControl} fullWidth required>
            <InputLabel id="demo-simple-select-label">Categorie</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={category}
              onChange={handleChangeCategorySelect}
            >
              {formInfos.map((info) => (
                <MenuItem value={info.categoryName}>{info.title}</MenuItem>
              ))}
            </Select>
          </FormControl>
          {formInfos.map(
            (info) =>
              info.tags !== undefined &&
              info.categoryName === category && (
                <FormControl className={classes.formControl} fullWidth required>
                  <InputLabel id="add-chip">Tags</InputLabel>
                  <Select
                    labelId="add-chip"
                    id="demo-mutiple-chip"
                    multiple
                    value={tagName}
                    onChange={handleChangeTagSelect}
                    input={<Input id="select-multiple-chip" />}
                    renderValue={(selected) => (
                      <div className={classes.chips}>
                        {selected.map((value) => (
                          <Chip
                            key={value}
                            label={value}
                            className={classes.chip}
                          />
                        ))}
                      </div>
                    )}
                    MenuProps={MenuProps}
                  >
                    {formInfos.map(
                      (info) =>
                        info.tags !== undefined &&
                        info.categoryName === category &&
                        info.tags.map((tag) => (
                          <MenuItem
                            key={tag}
                            value={tag}
                            style={getStyles(tag, tagName, theme)}
                          >
                            {tag}
                          </MenuItem>
                        ))
                    )}
                  </Select>
                </FormControl>
              )
          )}
          <input
            accept="image/*"
            className={classes.input}
            id="addImage"
            files={image}
            type="file"
            onChange={(e) => setImage(e.target.files[0])}
          />
          <label htmlFor="addImage">
            <Button
              fullWidth
              variant="contained"
              color="secondary"
              component="span"
              style={{ margin: "5px" }}
            >
              Ajouter une image
            </Button>
          </label>
          <Button fullWidth color="primary" type="submit" variant="contained">
            Ajouter un produit
          </Button>
        </form>
      </Collapse>
    </Container>
  );
};

export default AddProduct;
