import {
  Button,
  Chip,
  FormControl,
  Input,
  InputLabel,
  makeStyles,
  MenuItem,
  Modal,
  Select,
  TextField,
  useTheme,
} from "@material-ui/core";
import axios from "axios";
import React, { useState } from "react";
import { formInfos, _SERVER } from "../../addProductDatas/addProductDatas";

const EditProductModal = ({
  openEditModal,
  setOpenEditModal,
  product,
  setProducts,
  setLoading,
  setOpenLoginModal,
}) => {
  const { title, description, price, category, tag } = product;
  const [productTitle, setProductTitle] = useState(title);
  const [productDescription, setProductDescription] = useState(description);
  const [productPrice, setProductPrice] = useState(price);
  const [productCategory, setProductCategory] = useState(category);
  const [productTagName, setProductTagName] = useState(tag);

  const useStyles = makeStyles((theme) => ({
    root: {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      backgroundColor: "white",
      padding: theme.spacing(1),
    },
    modal: {
      marginTop: "50%",
    },
    formControl: {
      margin: theme.spacing(1),
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
    chips: {
      display: "flex",
      flexWrap: "wrap",
    },
    chip: {
      margin: 2,
    },
    noLabel: {
      marginTop: theme.spacing(3),
    },
    input: {
      display: "none",
    },
  }));
  function getStyles(name, tagName, theme) {
    return {
      fontWeight:
        tagName.indexOf(name) === -1
          ? theme.typography.fontWeightRegular
          : theme.typography.fontWeightMedium,
    };
  }
  const theme = useTheme();
  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 250,
      },
    },
  };
  const classes = useStyles();
  const handleChangeTagSelect = (event) => {
    setProductTagName(event.target.value);
  };
  const handleChangeCategorySelect = (event) => {
    setProductCategory(event.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const token = localStorage.getItem("token20hvin");
    if (token) {
      setLoading(true);
      let editedProduct = {}
      editedProduct.title = productTitle || title;
      editedProduct.description = productDescription;
      editedProduct.price = productPrice || price;
      editedProduct.category = productCategory || productCategory;
      editedProduct.tag = productTagName || tag;
      editedProduct.visibility = product.visibility;

      axios({
        method: "post",
        url: `${_SERVER}/api/products/updateProduct`,
        data: {
          update: editedProduct,
          productId: product._id,
          productCategory: product.category,
        },

        headers: {
          Authorization: "Bearer " + token,
        },
      })
        .then((response) => setProducts(response.data.data))
        .catch((error) => console.log(error))
        .finally(() => setLoading(false));
    } else {
      setOpenEditModal(false);
      setOpenLoginModal(true);
    }
  };
  return (
    <Modal
      open={openEditModal}
      onClose={() => setOpenEditModal(false)}
      className={classes.modal}
    >
      <form className={classes.root} onSubmit={handleSubmit}>
        <TextField
          id="title"
          label="Nom du produit"
          fullWidth
          value={productTitle}
          onChange={(e) => setProductTitle(e.target.value)}
          required
        />
        <TextField
          id="description"
          fullWidth
          multiline
          label="Description du produit"
          value={productDescription}
          onChange={(e) => setProductDescription(e.target.value)}
          rows={4}
        />
        <TextField
          id="price"
          fullWidth
          label="Prix du produit"
          required
          type="number"
          InputProps={{ inputProps: { min: 0, step: 0.5 } }}
          value={productPrice}
          onChange={(e) => setProductPrice(e.target.value)}
        />
        <FormControl className={classes.formControl} fullWidth required>
          <InputLabel id="demo-simple-select-label">Categorie</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={productCategory}
            onChange={handleChangeCategorySelect}
          >
            {formInfos.map((info) => (
              <MenuItem key={info} value={info.categoryName}>
                {info.title}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
        {formInfos.map(
          (info) =>
            info.tags !== undefined &&
            info.categoryName === productCategory && (
              <FormControl className={classes.formControl} fullWidth required>
                <InputLabel id="edit-chip">Tags</InputLabel>
                <Select
                  labelId="edit-chip"
                  id="demo-mutiple-chip"
                  multiple
                  value={productTagName}
                  onChange={handleChangeTagSelect}
                  input={<Input id="select-multiple-chip" />}
                  renderValue={(selected) => (
                    <div className={classes.chips}>
                      {selected.map((value) => (
                        <Chip
                          key={value}
                          label={value}
                          className={classes.chip}
                        />
                      ))}
                    </div>
                  )}
                  MenuProps={MenuProps}
                >
                  {formInfos.map(
                    (info) =>
                      info.tags !== undefined &&
                      info.categoryName === productCategory &&
                      info.tags.map((tag) => (
                        <MenuItem
                          key={tag}
                          value={tag}
                          style={getStyles(tag, productTagName, theme)}
                        >
                          {tag}
                        </MenuItem>
                      ))
                  )}
                </Select>
              </FormControl>
            )
        )}
        <Button color="primary" type="submit" variant="contained">
          Editer le produit {`${product.category} ${product.title}`}
        </Button>
      </form>
    </Modal>
  );
};

export default EditProductModal;
