import { ButtonGroup, IconButton, Typography } from "@material-ui/core";
import {
  Cancel,
  Edit,
  Search,
  Visibility,
  VisibilityOff,
  PhotoCamera,
} from "@material-ui/icons";
import axios from "axios";
import React, { useState } from "react";
import { Translate } from "react-auto-translate";
import { _SERVER } from "../../../addProductDatas/addProductDatas";
import EditProductImage from "../../EditProductImage";
import EditProductModal from "../../EditProductModal";
import ProductImageModal from "../ProductImageModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faWineBottle } from "@fortawesome/free-solid-svg-icons";

const ProductElement = ({
  product,
  handleDeleteProduct,
  setProducts,
  setLoading,
  user,
  setOpenLoginModal,
}) => {
  const [openEditModal, setOpenEditModal] = useState(false);
  const [openProductImageModal, setOpenProductImageModal] = useState(false);
  const [openEditProductImage, setOpenEditProductImage] = useState(false);

  const handleChangeVisibility = () => {
    let newProduct = { ...product };
    newProduct.visibility = !product.visibility;
    const token = localStorage.getItem("token20hvin");
    if (token) {
      setLoading(true);
      axios({
        method: "post",
        url: `${_SERVER}/api/products/updateProduct`,
        data: {
          update: newProduct,
          productId: product._id,
          productCategory: product.category,
        },
        headers: {
          Authorization: "Bearer " + token,
        },
      })
        .then((response) => setProducts(response.data.data))
        .catch((error) => console.log(error))
        .finally(() => setLoading(false));
    } else {
      setOpenLoginModal(true);
    }
  };

  return (
    <>
      <EditProductModal
        setOpenLoginModal={setOpenLoginModal}
        product={product}
        openEditModal={openEditModal}
        setOpenEditModal={setOpenEditModal}
        setProducts={setProducts}
        setLoading={setLoading}
      />
      <EditProductImage
        product={product}
        setProducts={setProducts}
        setLoading={setLoading}
        openEditProductImage={openEditProductImage}
        setOpenEditProductImage={setOpenEditProductImage}
        setOpenLoginModal={setOpenLoginModal}
      />
      <ProductImageModal
        {...product}
        openProductImageModal={openProductImageModal}
        setOpenProductImageModal={setOpenProductImageModal}
      />
      {user === "isAdmin" && (
        <ButtonGroup variant="contained" size="large">
          <IconButton
            size="medium"
            onClick={() => handleDeleteProduct(product?._id, product?.category)}
          >
            <Cancel
              fontSize="large"
              style={{
                color: "red",
                backgroundColor: "whitesmoke",
                borderRadius: "100%",
              }}
            />
          </IconButton>
          <IconButton size="medium" onClick={handleChangeVisibility}>
            {product && !product.visibility ? (
              <Visibility
                fontSize="large"
                style={{
                  color: "grey",
                  backgroundColor: "whitesmoke",
                  borderRadius: "100%",
                }}
              />
            ) : (
              <VisibilityOff
                fontSize="large"
                style={{
                  color: "grey",
                  backgroundColor: "whitesmoke",
                  borderRadius: "100%",
                }}
              />
            )}
          </IconButton>
          <IconButton size="medium" onClick={() => setOpenEditModal(true)}>
            <Edit
              fontSize="large"
              style={{
                color: "green",
                backgroundColor: "whitesmoke",
                borderRadius: "100%",
              }}
            />
          </IconButton>
          <IconButton
            size="medium"
            onClick={() => setOpenEditProductImage(true)}
          >
            <PhotoCamera
              fontSize="large"
              style={{
                color: "yellow",
                backgroundColor: "whitesmoke",
                borderRadius: "100%",
              }}
            />
          </IconButton>
        </ButtonGroup>
      )}
      {(user === "isAdmin" || product?.visibility) && (
        <>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flexGrow: 1,
              marginBottom: 2,
              marginTop: 8,
            }}
          >
            <div
              style={{
                display: "flex",
                flexGrow: 1,
                alignSelf: "flex-start",
                justifyContent: "space-between",
              }}
            >
              <Typography
                style={{
                  borderBottom: "1px solid #FF3B2D",
                  fontSize: "1.1em",
                }}
              >
                {product?.visibility
                  ? product?.title
                  : `Caché : ${product?.title}`}
                {product.image && (
                  <IconButton
                    color="inherit"
                    onClick={() => setOpenProductImageModal(true)}
                  >
                    <Search />
                  </IconButton>
                )}
              </Typography>
              <Typography variant="body1" component="span">
                {product?.price.toFixed(2)}€
              </Typography>
            </div>
          </div>
          <Typography
            variant="body2"
            component="p"
            style={{ marginBottom: 8, marginTop: 3 }}
          >
            <Translate>{product?.description}</Translate>
          </Typography>
          {(product?.category === "vins" ||
            product?.category === "champagnes") &&
            product?.tag.map((t) => (
              <Typography
                key={product?.category + t}
                variant="body2"
                component="span"
              >
                {" "}
                {(t === "Rouge" && product.category === "vins") ||
                (t === "Rosé" && product.category === "vins") ||
                (t === "Blanc" && product.category === "vins") ? (
                  <FontAwesomeIcon
                    size="2x"
                    icon={faWineBottle}
                    style={
                      t === "Rouge"
                        ? { color: "#722f37" }
                        : t === "Blanc"
                        ? { color: "#f1f285" }
                        : t === "Rosé"
                        ? { color: "#ee7272" }
                        : { color: "inherit" }
                    }
                  />
                ) : (
                  t
                )}{" "}
              </Typography>
            ))}
        </>
      )}
    </>
  );
};

export default ProductElement;
