import {
  Button,
  Container,
  makeStyles,
  Modal,
  Snackbar,
  TextField,
  Typography,
} from "@material-ui/core";
import axios from "axios";
import Alert from "@material-ui/lab/Alert";
import React, { useState } from "react";
import { _SERVER } from "../../addProductDatas/addProductDatas";

const LoginModal = ({ openLoginModal, setOpenLoginModal, setUser }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");
  const [error, setError] = useState(false);
  const [open, setOpen] = useState(false);
  const useStyles = makeStyles((theme) => ({
    root: {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      backgroundColor: "white",
      padding: theme.spacing(1),
    },
    modal: {
      marginTop: "50%",
    },
    input: {
      margin: theme.spacing(3),
    },
  }));
  const classes = useStyles();
  const handleSubmit = (e) => {
    e.preventDefault();
    axios({
      method: "post",
      url: `${_SERVER}/auth/login`,
      data: {
        email,
        password,
      },
    }).then((response) => {
      if (response && response.data.status === 200) {
        setError(false);
        setUser(response.data.role);
        localStorage.setItem("token20hvin", response.data.token);
        setMessage(response.data.message);
        setTimeout(() => {
          setOpenLoginModal(false);
        }, 5000);
      } else {
        setError(true);
        setMessage(response.data.message);
      }
    });
  };
  return (
    <Modal
      className={classes.modal}
      open={openLoginModal}
      onClose={() => setOpenLoginModal(false)}
    >
      <form className={classes.root} onSubmit={handleSubmit}>
        <Container
          style={{ borderBottom: "1px solid black", textAlign: "center" }}
        >
          <Typography variant="h6">Se connecter</Typography>
        </Container>
        <Snackbar
          open={message ? true : false}
          autoHideDuration={5000}
          onClose={() => setOpen(false)}
        >
          <Alert
            severity={error ? "error" : "success"}
            onClose={() => setOpen(false)}
          >
            {message}
          </Alert>
        </Snackbar>
        <TextField
          error={error}
          className={classes.input}
          label="Email"
          type="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
          fullWidth
        />
        <TextField
          error={error}
          className={classes.input}
          label="Mot de passe"
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
          fullWidth
        />
        <Button
          className={classes.input}
          type="submit"
          variant="contained"
          color="primary"
          fullWidth
        >
          Se Connecter
        </Button>
      </form>
    </Modal>
  );
};

export default LoginModal;
