import { Box, Container, makeStyles, Typography } from "@material-ui/core";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import { formInfos, _SERVER } from "../../addProductDatas/addProductDatas";
import Loader from "../../component/small/Loader";
import ProductElement from "../../component/small/ProductElement";
import TagSelector from "../../component/small/TagSelector";

const ShowProduct = ({
  product,
  products,
  setProducts,
  user,
  setOpenLoginModal,
}) => {
  const [loading, setLoading] = useState(false);
  const { category } = useParams();
  const [tags, setTags] = useState([]);
  const [filter, setFilter] = useState("");
  const [filteredProducts, setFilteredProducts] = useState(products);

  const useStyles = makeStyles((theme) => ({
    titleWrapper: {
      display: "flex",
      justifyContent: "center",
    },
    title: {
      display: "inline",
      borderBottom: "2px solid #FF3B2D",
      margin: theme.spacing(2),
    },
  }));
  const classes = useStyles();

  const filterProducts = (array, filter) => {
    if (filter) {
      return array.filter((p) => p.tag.includes(filter));
    } else {
      return array;
    }
  };
  useEffect(() => {
    if (filter === "") {
      setFilteredProducts(products);
    } else {
      setFilteredProducts(filterProducts(products, filter));
    }
  }, [filter, category, products]);
  useEffect(() => {
    setFilter("");
    let currentTags = formInfos.filter(
      (info) => info.categoryName === category
    );
    setTags(currentTags[0].tags);
    setLoading(true);
    axios({
      method: "get",
      url: `${_SERVER}/api/products/${category}`,
    })
      .then((response) => {
        setProducts(response.data.data);
      })
      .catch((error) => console.log(error))
      .finally(() => setLoading(false));
  }, [category]);

  const handleDeleteProduct = (productId, productCategory = category) => {
    const token = localStorage.getItem("token20hvin");
    if (token) {
      setLoading(true);
      axios({
        method: "delete",
        url: `${_SERVER}/api/products/deleteProduct`,
        data: {
          productId,
          productCategory,
        },
        headers: {
          Authorization: "Bearer " + token,
        },
      })
        .then((response) => setProducts(response.data.data))
        .catch((error) => console.log(error))
        .finally(() => setLoading(false));
    } else {
      setOpenLoginModal(true);
    }
  };



  return (
    <Container className={classes.root} component="div">
      {tags && (
        <TagSelector tags={tags} setFilter={setFilter} products={products} />
      )}
      <Container>
        {!loading && (
          <>
            <Box component="div" className={classes.titleWrapper}>
              <Typography variant="h4" component="h2" className={classes.title}>
                {product}
              </Typography>
            </Box>
            {filteredProducts &&
              filteredProducts
                .sort((a, b) => a.price - b.price)
                .map((product) => (
                  <ProductElement
                    key={product?._id}
                    product={product}
                    handleDeleteProduct={handleDeleteProduct}
                    setProducts={setProducts}
                    setLoading={setLoading}
                    user={user}
                    setOpenLoginModal={setOpenLoginModal}
                  />
                ))}
          </>
        )}
        {loading && <Loader loading={loading} />}
      </Container>
    </Container>
  );
};

export default ShowProduct;
