import { useState } from "react";
import { Translator } from "react-auto-translate";
import { Redirect, Route, Switch } from "react-router";
import { _GOOGLE_API_KEY } from "../../addProductDatas/addProductDatas";
import AddProduct from "../../pages/AddProductPage/AddProduct";
import ShowProduct from "../../pages/ShowProduct";
import LoginModal from "../LoginModal";
import Copyright from "../small/Copyright";
import NavigationBar from "../small/navigationBar";
import TopAppBar from "../small/TopAppBar";

import "./App.css";

const App = () => {
  const [product, setProduct] = useState("");
  const [products, setProducts] = useState([]);
  const [openLoginModal, setOpenLoginModal] = useState(false);
  const [user, setUser] = useState("");
  const [showAddProduct, setShowAddProduct] = useState(false);
  let userLang = navigator.language || navigator.userLanguage;
  const cacheProvider = {
    get: (language, key) =>
      ((JSON.parse(localStorage.getItem("translations")) || {})[key] || {})[
        language
      ],
    set: (language, key, value) => {
      const existing = JSON.parse(localStorage.getItem("translations")) || {
        [key]: {},
      };
      existing[key] = { ...existing[key], [language]: value };
      localStorage.setItem("translations", JSON.stringify(existing));
    },
  };
  return (
    <>
      <Translator
        cacheProvider={cacheProvider}
        from="fr"
        to={userLang}
        googleApiKey={_GOOGLE_API_KEY}
      >
        <div className="App">
          <TopAppBar setOpenLoginModal={setOpenLoginModal} user={user} />
          <LoginModal
            openLoginModal={openLoginModal}
            setUser={setUser}
            setOpenLoginModal={setOpenLoginModal}
          />
          <NavigationBar setProduct={setProduct} />
          <Switch>
            <Route exact path="/">
              <Redirect to="/produits/antipasti" />
            </Route>
            <Route path={`/produits/:category`}>
              {user === "isAdmin" && (
                <AddProduct
                  showAddProduct={showAddProduct}
                  setShowAddProduct={setShowAddProduct}
                  products={products}
                  setProducts={setProducts}
                  setOpenLoginModal={setOpenLoginModal}
                />
              )}
              <ShowProduct
                products={products}
                setProducts={setProducts}
                product={product}
                user={user}
                setOpenLoginModal={setOpenLoginModal}
              />
            </Route>
          </Switch>
        </div>
      </Translator>
      <Copyright />
    </>
  );
};

export default App;
