import { Modal } from "@material-ui/core";
import React from "react";

const ProductImageModal = ({
  image,
  title,
  openProductImageModal,
  setOpenProductImageModal,
}) => {
  const arrayBufferToBase64 = (buffer) => {
    let binary = "";
    const bytes = [].slice.call(new Uint8Array(buffer));
    bytes.forEach((b) => (binary += String.fromCharCode(b)));
    return window.btoa(binary);
  };
  const base64Flag = `data:${image?.contentType};base64,`;
  const imageStr = arrayBufferToBase64(image?.data?.data);
  return (
    <Modal
      style={{ marginTop: "50%" }}
      open={openProductImageModal}
      onClose={() => setOpenProductImageModal(false)}
    >
      <img
        style={{ width: "100%", objectFit: "cover" }}
        src={base64Flag + imageStr}
        alt={title}
      />
    </Modal>
  );
};

export default ProductImageModal;
